import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../../store/store';
import { Card } from '../../types';
import CardForm from './CardForm';
import api from '../../services/api';

const CreateCard: React.FC = () => {
  const { user } = useSelector((state: RootState) => state.auth);
  const [showForm, setShowForm] = useState(false);
  const [success, setSuccess] = useState<string>('');
  const [error, setError] = useState<string>('');
  const [cards, setCards] = useState<Card[]>([]);
  const [loading, setLoading] = useState(false);
  const [editingCard, setEditingCard] = useState<Card | null>(null);
  const [showCardList, setShowCardList] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [filteredCards, setFilteredCards] = useState<Card[]>([]);

  // 
  useEffect(() => {
    if (showCardList) {
      fetchCards();
    }
  }, [showCardList]);

  // 
  useEffect(() => {
    if (!searchTerm.trim()) {
      setFilteredCards(cards);
    } else {
      const filtered = cards.filter(card =>
        card.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        card.type.toLowerCase().includes(searchTerm.toLowerCase()) ||
        card.category.toLowerCase().includes(searchTerm.toLowerCase()) ||
        card.faction.toLowerCase().includes(searchTerm.toLowerCase()) ||
        card.effect.toLowerCase().includes(searchTerm.toLowerCase()) ||
        card.createdBy.username.toLowerCase().includes(searchTerm.toLowerCase())
      );
      setFilteredCards(filtered);
    }
  }, [cards, searchTerm]);

  const fetchCards = async () => {
    try {
      setLoading(true);
      const response = await api.get('/cards');
      // 
      const cardsData = response.data?.cards || response.data || [];
      setCards(Array.isArray(cardsData) ? cardsData : []);
    } catch (error) {
      console.error(':', error);
      setError('');
      setCards([]); // 
    } finally {
      setLoading(false);
    }
  };

  const handleCreateCard = async (cardData: Partial<Card>) => {
    try {
      setError('');
      setSuccess('');
      
      await api.cards.create(cardData);
      setSuccess('');
      setShowForm(false);
      
      // 
      if (showCardList) {
        fetchCards();
      }
      
      // 3
      setTimeout(() => {
        setSuccess('');
      }, 3000);
    } catch (error: any) {
      console.error(':', error);
      setError(error.response?.data?.message || '');
    }
  };

  const handleEditCard = async (cardData: Partial<Card>) => {
    if (!editingCard) return;
    
    try {
      setError('');
      setSuccess('');
      
      await api.cards.update(editingCard._id, cardData);
      setSuccess('');
      setShowForm(false);
      setEditingCard(null);
      
      // 
      fetchCards();
      
      // 3
      setTimeout(() => {
        setSuccess('');
      }, 3000);
    } catch (error: any) {
      console.error(':', error);
      setError(error.response?.data?.message || '');
    }
  };

  const handleDeleteCard = async (cardId: string) => {
    if (!window.confirm('')) {
      return;
    }
    
    try {
      setError('');
      setSuccess('');
      
      await api.cards.delete(cardId);
      setSuccess('');
      
      // 
      fetchCards();
      
      // 3
      setTimeout(() => {
        setSuccess('');
      }, 3000);
    } catch (error: any) {
      console.error(':', error);
      setError(error.response?.data?.message || '');
    }
  };

  const startEditCard = (card: Card) => {
    setEditingCard(card);
    setShowForm(true);
    setShowCardList(false);
  };

  // 
  if (!user?.isAdmin) {
    return (
      <div className="max-w-7xl mx-auto p-6">
        <h2 className="text-2xl font-bold mb-6 text-white"></h2>
        <div className="bg-yellow-100 border border-yellow-400 text-yellow-700 px-4 py-3 rounded">
          
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto p-6">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-2xl font-bold text-white"></h2>
      </div>

      {error && (
        <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
          {error}
        </div>
      )}

      {success && (
        <div className="mb-4 p-3 bg-green-100 border border-green-400 text-green-700 rounded">
          {success}
        </div>
      )}

      {!showForm && !showCardList ? (
        <div className="bg-white rounded-lg shadow-md p-8 text-center">
          <div className="mb-6">
            <div className="text-6xl mb-4"></div>
            <h3 className="text-2xl font-bold text-gray-800 mb-4"></h3>
            <p className="text-gray-600 mb-8">
              
            </p>
          </div>
          
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <button
              onClick={() => setShowForm(true)}
              className="bg-green-600 hover:bg-green-700 text-white px-8 py-4 rounded-lg font-semibold text-lg transition-colors flex items-center space-x-3"
            >
              <span></span>
              <span></span>
            </button>
            
            <button
              onClick={() => setShowCardList(true)}
              className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-4 rounded-lg font-semibold text-lg transition-colors flex items-center space-x-3"
            >
              <span></span>
              <span></span>
            </button>
          </div>
          
          <div className="mt-8 grid grid-cols-1 md:grid-cols-2 gap-8 text-left">
            <div>
              <h4 className="font-semibold text-gray-800 mb-3"></h4>
              <ul className="text-gray-600 space-y-2">
                <li className="flex items-start">
                  <span className="text-blue-500 mr-2"></span>
                  
                </li>
                <li className="flex items-start">
                  <span className="text-blue-500 mr-2"></span>
                  
                </li>
                <li className="flex items-start">
                  <span className="text-blue-500 mr-2"></span>
                  
                </li>
                <li className="flex items-start">
                  <span className="text-blue-500 mr-2"></span>
                  URL
                </li>
              </ul>
            </div>
            
            <div>
              <h4 className="font-semibold text-gray-800 mb-3"></h4>
              <div className="space-y-3 text-sm">
                <div className="bg-gray-50 p-3 rounded">
                  <div className="font-medium text-gray-800"></div>
                  <ul className="text-gray-600 mt-1 space-y-1">
                    <li><span className="font-medium">:</span> </li>
                    <li><span className="font-medium">:</span> //</li>
                    <li><span className="font-medium">:</span> </li>
                    <li><span className="font-medium">:</span> </li>
                    <li><span className="font-medium">:</span> </li>
                  </ul>
                </div>
                
                <div className="bg-gray-50 p-3 rounded">
                  <div className="font-medium text-gray-800"></div>
                  <ul className="text-gray-600 mt-1 space-y-1">
                    <li><span className="font-medium">:</span> </li>
                    <li><span className="font-medium">:</span> </li>
                  </ul>
                </div>
                
                <div className="bg-gray-50 p-3 rounded">
                  <div className="font-medium text-gray-800"></div>
                  <ul className="text-gray-600 mt-1 space-y-1">
                    <li><span className="font-medium">:</span> </li>
                    <li><span className="font-medium">:</span> </li>
                  </ul>
                </div>
                
                <div className="bg-gray-50 p-3 rounded">
                  <div className="font-medium text-gray-800"></div>
                  <ul className="text-gray-600 mt-1 space-y-1">
                    <li><span className="font-medium">:</span> URL</li>
                    <li><span className="font-medium">:</span> /</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      ) : showCardList ? (
        <div>
          <div className="mb-4">
            <button
              onClick={() => {
                setShowCardList(false);
                setError('');
              }}
              className="text-gray-400 hover:text-white transition-colors flex items-center"
            >
              <span className="mr-2"></span>
              
            </button>
          </div>
          
          <div className="bg-white rounded-lg shadow-md p-6">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-semibold"> ({filteredCards.length}/{cards.length})</h3>
              <button
                onClick={() => {
                  setShowCardList(false);
                  setShowForm(true);
                }}
                className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg"
              >
                
              </button>
            </div>
            
            {/*  */}
            <div className="mb-4">
              <div className="relative">
                <input
                  type="text"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  placeholder="..."
                  className="w-full px-4 py-2 pl-10 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <svg className="h-5 w-5 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                  </svg>
                </div>
                {searchTerm && (
                  <button
                    onClick={() => setSearchTerm('')}
                    className="absolute inset-y-0 right-0 pr-3 flex items-center"
                  >
                    <svg className="h-5 w-5 text-gray-400 hover:text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                )}
              </div>
            </div>
            
            {loading ? (
              <div className="text-center py-8">
                <div className="text-gray-500">...</div>
              </div>
            ) : filteredCards.length === 0 ? (
              <div className="text-center py-8">
                <div className="text-gray-500">
                  {searchTerm ? '' : ''}
                </div>
              </div>
            ) : (
              <div className="space-y-4">
                {filteredCards.map((card) => (
                  <div key={card._id} className="bg-white border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow">
                    <div className="flex items-start justify-between">
                      {/*  */}
                      <div className="flex items-start space-x-4 flex-1">
                        {/*  */}
                        {card.image && (
                          <img
                            src={card.image}
                            alt={card.name}
                            className="w-16 h-16 rounded-lg object-cover flex-shrink-0"
                            onError={(e) => {
                              const target = e.target as HTMLImageElement;
                              target.style.display = 'none';
                            }}
                          />
                        )}
                        
                        {/*  */}
                        <div className="flex-1 min-w-0">
                          {/*  */}
                          <div className="flex items-center space-x-3 mb-2">
                            <h4 className="text-lg font-semibold text-gray-900 truncate">
                              {card.name}
                            </h4>
                            <span className={`px-2 py-1 text-xs rounded-full flex-shrink-0 ${
                              card.type === '' ? 'bg-blue-100 text-blue-800' :
                              card.type === '' ? 'bg-green-100 text-green-800' :
                              card.type === '' ? 'bg-purple-100 text-purple-800' :
                              'bg-gray-100 text-gray-800'
                            }`}>
                              {card.type}
                            </span>
                            <span className="font-bold text-orange-600 text-lg flex-shrink-0">
                              {card.cost}
                            </span>
                            {card.attack !== undefined && card.health !== undefined && (
                              <span className="font-mono flex-shrink-0">
                                <span className="text-red-600 font-bold">{card.attack || 0}</span>
                                <span className="text-gray-400 mx-1">/</span>
                                <span className="text-green-600 font-bold">{card.health || 0}</span>
                              </span>
                            )}
                          </div>
                          
                          {/*  */}
                          <div className="flex items-center space-x-4 mb-2 text-sm text-gray-600">
                            <span><strong>:</strong> {card.category}</span>
                            <span><strong>:</strong> {card.faction}</span>
                            <span>
                              {card.isPublic ? (
                                <span className="text-green-600 font-medium"></span>
                              ) : (
                                <span className="text-gray-500"></span>
                              )}
                            </span>
                            <span><strong>:</strong> {card.createdBy.username}</span>
                          </div>
                          
                          {/*  */}
                          <div className="text-sm text-gray-700">
                            <strong>:</strong> {card.effect}
                          </div>
                        </div>
                      </div>
                      
                      {/*  */}
                      <div className="flex flex-col space-y-2 ml-4 flex-shrink-0">
                        <button
                          onClick={() => startEditCard(card)}
                          className="px-3 py-1 text-sm bg-blue-100 text-blue-700 rounded hover:bg-blue-200 transition-colors"
                        >
                          
                        </button>
                        <button
                          onClick={() => handleDeleteCard(card._id)}
                          className="px-3 py-1 text-sm bg-red-100 text-red-700 rounded hover:bg-red-200 transition-colors"
                        >
                          
                        </button>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      ) : (
        <div>
          <div className="mb-4">
            <button
              onClick={() => {
                setShowForm(false);
                setEditingCard(null);
                setError('');
                if (cards.length > 0) {
                  setShowCardList(true);
                }
              }}
              className="text-gray-400 hover:text-white transition-colors flex items-center"
            >
              <span className="mr-2"></span>
              {cards.length > 0 ? '' : ''}
            </button>
          </div>
          
          <CardForm
            card={editingCard || undefined}
            onSubmit={editingCard ? handleEditCard : handleCreateCard}
            onCancel={() => {
              setShowForm(false);
              setEditingCard(null);
              setError('');
              if (cards.length > 0) {
                setShowCardList(true);
              }
            }}
          />
        </div>
      )}
    </div>
  );
};

export default CreateCard;
