import React, { useState } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../../store/store';
import api from '../../services/api';

const DataExport: React.FC = () => {
  const { user } = useSelector((state: RootState) => state.auth);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string>('');
  const [success, setSuccess] = useState<string>('');

  const handleExport = async (type: 'cards' | 'heroes' | 'all') => {
    if (!user || !user.isAdmin) {
      setError('');
      return;
    }

    setLoading(true);
    setError('');
    setSuccess('');

    try {
      let endpoint = '';
      let filename = '';
      let responseType: 'blob' | 'json' = 'blob';

      switch (type) {
        case 'cards':
          endpoint = '/export/cards';
          filename = 'cards_export.csv';
          break;
        case 'heroes':
          endpoint = '/export/heroes';
          filename = 'heroes_export.csv';
          break;
        case 'all':
          endpoint = '/export/all';
          filename = 'database_export.json';
          responseType = 'json';
          break;
      }

      const response = await api.get(endpoint, {
        responseType: responseType,
      });

      if (responseType === 'json') {
        // JSON
        const jsonString = JSON.stringify(response.data, null, 2);
        const blob = new Blob([jsonString], { type: 'application/json' });
        const url = window.URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', filename);
        document.body.appendChild(link);
        link.click();
        link.remove();
        window.URL.revokeObjectURL(url);
      } else {
        // CSV
        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', filename);
        document.body.appendChild(link);
        link.click();
        link.remove();
        window.URL.revokeObjectURL(url);
      }

      setSuccess(`${getTypeDisplayName(type)}`);
    } catch (error: any) {
      setError(error.response?.data?.message || `${getTypeDisplayName(type)}`);
    } finally {
      setLoading(false);
    }
  };

  const getTypeDisplayName = (type: 'cards' | 'heroes' | 'all') => {
    switch (type) {
      case 'cards':
        return '';
      case 'heroes':
        return '';
      case 'all':
        return '';
      default:
        return '';
    }
  };

  if (!user?.isAdmin) {
    return (
      <div className="max-w-4xl mx-auto p-6">
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
          
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h2 className="text-2xl font-bold mb-6"></h2>
      
      <div className="bg-white rounded-lg shadow-md p-6 mb-6">
        <div className="mb-4">
          <h3 className="text-lg font-semibold mb-2"></h3>
          <ul className="list-disc list-inside text-sm text-gray-600 space-y-1">
            <li>CSV</li>
            <li>CSV</li>
            <li>JSON</li>
            <li>CSV</li>
            <li>JSON</li>
          </ul>
        </div>
      </div>

      {error && (
        <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
          {error}
        </div>
      )}

      {success && (
        <div className="mb-4 p-3 bg-green-100 border border-green-400 text-green-700 rounded">
          {success}
        </div>
      )}

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/*  */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h3 className="text-lg font-semibold mb-4 text-blue-600"></h3>
          <div className="mb-4">
            <p className="text-sm text-gray-600 mb-2">
              CSV
            </p>
            <ul className="text-xs text-gray-500 space-y-1">
              <li> </li>
              <li> </li>
              <li> Excel</li>
            </ul>
          </div>
          <button
            onClick={() => handleExport('cards')}
            disabled={loading}
            className={`w-full py-2 px-4 rounded-md text-white font-medium
              ${loading
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-blue-500 hover:bg-blue-600'
              }`}
          >
            {loading ? '...' : ''}
          </button>
        </div>

        {/*  */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h3 className="text-lg font-semibold mb-4 text-green-600"></h3>
          <div className="mb-4">
            <p className="text-sm text-gray-600 mb-2">
              CSV
            </p>
            <ul className="text-xs text-gray-500 space-y-1">
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
          <button
            onClick={() => handleExport('heroes')}
            disabled={loading}
            className={`w-full py-2 px-4 rounded-md text-white font-medium
              ${loading
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-green-500 hover:bg-green-600'
              }`}
          >
            {loading ? '...' : ''}
          </button>
        </div>

        {/*  */}
        <div className="bg-white rounded-lg shadow-md p-6">
          <h3 className="text-lg font-semibold mb-4 text-purple-600"></h3>
          <div className="mb-4">
            <p className="text-sm text-gray-600 mb-2">
              JSON
            </p>
            <ul className="text-xs text-gray-500 space-y-1">
              <li> </li>
              <li> </li>
              <li> </li>
            </ul>
          </div>
          <button
            onClick={() => handleExport('all')}
            disabled={loading}
            className={`w-full py-2 px-4 rounded-md text-white font-medium
              ${loading
                ? 'bg-gray-400 cursor-not-allowed'
                : 'bg-purple-500 hover:bg-purple-600'
              }`}
          >
            {loading ? '...' : ''}
          </button>
        </div>
      </div>

      <div className="mt-8 bg-yellow-50 border border-yellow-200 rounded-lg p-4">
        <h4 className="font-medium text-yellow-800 mb-2"></h4>
        <ul className="text-sm text-yellow-700 space-y-1">
          <li> </li>
          <li> CSVUTF-8</li>
          <li> </li>
          <li> </li>
          <li> CSV</li>
        </ul>
      </div>
    </div>
  );
};

export default DataExport;
